.userdialog {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.box {
  width: 500px;
}

.box > * {
  margin: 10px
}

.box > p > span {
  color: rgb(24,144,255);
  cursor: pointer;
  text-decoration: underline;
}

.box > h1 {
  text-align: center
}